<template>
		<div class="follow  paddtop">
			    <div class="list">
				<uc-cell
				v-for="(item ,index) in list"
				:data="item"
				:key="item._id"
				collectionName="follow" 
				isAdd 
				:index="index"
				@add-item="show"
				>
				</uc-cell>
		        </div>			
		</div>
</template>

<script>
	// 4.  cell
	import UcCell from '../components/zy-cell/zy-cell.vue'
	
	export default {
	  name: 'follow',
	  components:{
		UcCell
	  },
	  data(){
		  return{
			 
			  list:[]
		  }
	  },
	  methods:{
	    show(ev){
	      console.log(ev)
	    }
	  },
	  mounted(){
	  		this.$axios({
	  			url:'/api/follow',
	  				// params:{_limit:15 , _page:1}
	  		}).then(
	  		    res=>this.list=res.data.data
	  		)
	  }
	}
</script>

