<template>
<div id="app"> 
   <!-- <Header></Header>
	<Foot></Foot>
	 -->
	<div class="cs">
		<van-notice-bar scrollable text=" " />
	</div>
	
	
	<div class="home  paddtop">
		    <Banner
		      :data="banner"
		      collectionName="banner"
		    >
			</Banner>
			<router-view></router-view>
		    <div class="list">
			<uc-cell
			v-for="(item ,index) in list"
			:data="item"
			:key="item._id"
			collectionName="home" 
			isAdd 
			:index="index"
			@add-item="show"
			>
			</uc-cell>
	        </div>
		
	</div>
	
	
</div>
</template>

<script>

// 
// import *** from '*****'
// 1.  
import Header from '../layouts/Header.vue'
// 2.   
import Foot from '../layouts/Foot.vue'
// 3.  banner  
import Banner from '../components/zy-banner/banner.vue'
// 4.  cell
import UcCell from '../components/zy-cell/zy-cell.vue'
// 5.   axios  
import axios from 'axios'
export default {
  name: 'App',
  components:{
	Header, Foot ,Banner ,UcCell
  },
  data(){
	  return{
		  banner:[],
		  list:[],
		  
	  }
  },
  methods:{
    show(ev){
      console.log(ev)
    }	
  },
  mounted(){
	  axios({
	  	url:'/api/home',
		params:{_limit:20 , _page:1}
	  }).then(
	      res=>this.list=res.data.data
	  )
	  
	  axios({
	  	url:'/api/banner',
	  		params:{_limit:3 , _page:1}
	  }).then(
	      res=>this.banner=res.data.data
	  )
	  
	 //  axios({
	 //  	url:'/api/home',
		
	 //  }).then(
	 //      res=>console.log('res',res)
	 //  )
  }
}
</script>

<style>
.paddtop {
    padding-top: 0.0625rem;
}
.cs{
	margin-top: 0.7rem;
}
</style>
