<template>
  <div id="app"> 
    <loading v-show="$root.bLoading"/>
    <Header v-show="$root.bNav"/>
	<router-view/>
	<Foot  v-show="$root.bFoot"/>
	
  </div>
</template>

<script>

// 
// import *** from '*****'
import Header from './Header.vue'
import Foot from './Foot.vue'
import loading from '../components/zy-loading/zy-loading.vue'
export default {
  name: 'App',
  data(){
	  return{}
  },
  components:{
	Header, Foot ,loading
  },
   watch:{
      $route:{
        handler(newVal){
          let path = newVal.path;
          if(/home|follow|column/.test(path)){
            this.$root.bNav=true;
            this.$root.bFoot=true;
          }
          if(/user/.test(path)){
            this.$root.bNav=false;
            this.$root.bFoot=true;
          }
          if(/login|reg|detail/.test(path)){
            this.$root.bNav=false;
            this.$root.bFoot=false;
          }
        },
        immediate:true
      }
    },

  
}
</script>

