import {datas} from './datas.js';

export default time => {
  let d = new Date();
  d.setTime(time);
  let year = d.getFullYear();
  let mon = d.getMonth()+1;
  let date = d.getDate();
  let hour = d.getHours();
  let min = d.getMinutes();
  let sec = d.getSeconds();
  
  return `${year}-${datas(mon)}-${datas(date)} ${datas(hour)}:${datas(min)}:${datas(sec)}`;
  
}