<template>
  <div class="login">
	  <!--  -->
	<!-- <zy-nav bg-color="transparent" :border-width="0" arrow="gray"></zy-nav> -->
	  <uc-nav bg-color="transparent" :border-width="0" arrow="gray"></uc-nav>
    <h1></h1>
    <div class="login-box">
      <p class="lsolid"></p>
      <div class="login">
        <router-link to="/login"></router-link>
        <span></span>
        <router-link to="/reg"></router-link>
      </div>
      <p class="rsolid"></p>
    </div>
    <ul>
  
      <!-- {{mess}} -->
	  <zy-input v-model="username"   label="" ></zy-input>
	  <zy-input v-model="password"   label="" type="password"  ></zy-input>
       {{mess}}
	</ul>
    <div class="footbox">
      <!-- <uc-button @click="login" style="margin-top:.23rem"> </uc-button> -->
	  <zy-button @clickaa="logins"  style="margin-top:.23rem" > </zy-button>
      <a href="javascript:;" class="tishi"></a>
    </div>
  </div>
  
</template>

<script>
	import UcNav from '../components/zy-nav/zy-test.vue'
	import ZyInput from '../components/zy-input/zy-input.vue'
	import ZyButton from '../components/zy-button/zy-button.vue'
	// 5.   axios
	import axios from 'axios'
	
	
	export default {
	  name: 'login',
	  data(){
	    return {
	      username:'',
	      password:'',
		  mess:'',
	    }
	  },
	  components: {
		UcNav , ZyInput , ZyButton
	  },
	  methods:{
		     logins(){
				axios({
					url:'/api/login',
					 method:'post', 
					 data:{
					   username:this.username,
					   password:this.password
					 }
				}).then(
				    res=>{
						 if(res.data.err===0){
						              //local  
						              window.localStorage.setItem('user',JSON.stringify(res.data));
						              if(!this.$route.query.p){
						                this.$router.push('/user')
										
						              }else{
										
						                this.$router.push(this.$route.query.p)
								
						              }
						            }else{
						              this.mess = res.data.msg
						            }
					}
				)
				
				
				
		        }
	  },
	  
	}
</script>


